.loadingContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 90vh;
  margin-top: -2vw; }

.button {
  font-weight: bold;
  padding: .5vw 1vw;
  border-radius: 20px;
  font-size: .8vw;
  border: none;
  background: white;
  text-decoration: none;
  color: black;
  transition: all .2s ease-in-out; }
  .button:hover {
    opacity: .8;
    cursor: pointer; }
  .button:visited {
    color: black; }

a {
  text-decoration: none; }

@keyframes backgroundBooklickColors {
  0% {
    background-color: #F6416C; }
  33% {
    background-color: #2ABDEF; }
  66% {
    background-color: #1FCFCB; }
  100% {
    background-color: #F6416C; } }

.carouselContainer {
  position: relative;
  text-align: center;
  height: 89.2vh;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  -webkit-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease; }
  .carouselContainer .rotateBackground {
    width: 100vw;
    height: 100%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    overflow: hidden; }
    .carouselContainer .rotateBackground .rotateGradient {
      width: 165vw;
      height: 165vw;
      animation: backgroundGradient 3s ease infinite; }
      @media (min-width: 1800px) {
        .carouselContainer .rotateBackground .rotateGradient {
          width: 190vw;
          height: 190vw; } }
  .carouselContainer .contentContainer {
    z-index: 1;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100vw;
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 45vw; }
    .carouselContainer .contentContainer .logoContainer {
      width: 10vw;
      display: flex;
      align-items: center;
      justify-content: center;
      height: 10vw;
      border-radius: 50%;
      overflow: hidden;
      margin-bottom: 2%; }
      .carouselContainer .contentContainer .logoContainer img {
        width: 150%; }
    .carouselContainer .contentContainer .titleCountry {
      margin-bottom: 3vw; }
    .carouselContainer .contentContainer .countriesContainer {
      display: flex; }
    .carouselContainer .contentContainer h1 {
      font-size: 4vw;
      font-weight: bold;
      margin: 0; }
    .carouselContainer .contentContainer h2 {
      margin: 0;
      font-size: 2vw; }
    .carouselContainer .contentContainer .search {
      position: relative; }
      .carouselContainer .contentContainer .search .inputSearch {
        position: relative;
        background-color: black;
        overflow: hidden;
        align-items: center;
        width: 35vw;
        margin-top: 2%;
        display: flex;
        border-radius: 5px;
        border-bottom: white solid 3px;
        opacity: 0.7;
        padding: .5vw 2vw;
        -webkit-transition: all 0.5s ease;
        -o-transition: all 0.5s ease;
        transition: all 0.5s ease; }
        .carouselContainer .contentContainer .search .inputSearch > div {
          background: #141414;
          text-align: left;
          font-size: 1.2vw; }
          .carouselContainer .contentContainer .search .inputSearch > div div {
            padding: .5vw 2.5vw;
            cursor: pointer; }
            .carouselContainer .contentContainer .search .inputSearch > div div:hover {
              background: #1c1c1c; }
            .carouselContainer .contentContainer .search .inputSearch > div div mark {
              padding: 0;
              background: none;
              color: white;
              font-weight: 900; }
      .carouselContainer .contentContainer .search input {
        width: 100%;
        height: fit-content;
        box-shadow: none;
        border: none;
        background: transparent;
        color: #fff;
        font-size: 1.5vw; }
        .carouselContainer .contentContainer .search input:focus {
          outline: none; }
      .carouselContainer .contentContainer .search .searchButton {
        color: white;
        display: block;
        top: 0;
        right: 0;
        font-size: 2vw;
        cursor: pointer;
        margin-right: -1vw; }
      .carouselContainer .contentContainer .search .autocomplete {
        background: white;
        margin-top: .3%;
        color: black;
        border-radius: 5px;
        box-shadow: 0 0 1vw -.6vw black;
        position: absolute;
        max-height: 245%;
        overflow: scroll;
        width: 100%; }
        .carouselContainer .contentContainer .search .autocomplete .university {
          display: flex;
          padding: 1vw;
          text-decoration: none;
          transition: all .2s ease-in-out; }
          .carouselContainer .contentContainer .search .autocomplete .university:hover {
            background: #f0f0f0;
            cursor: pointer; }
          .carouselContainer .contentContainer .search .autocomplete .university:visited {
            color: black; }
          .carouselContainer .contentContainer .search .autocomplete .university .logoContainer {
            width: 3vw;
            height: 3vw;
            border: 1px solid rgba(72, 72, 72, 0.2);
            margin-bottom: 0; }
            .carouselContainer .contentContainer .search .autocomplete .university .logoContainer img {
              width: 100%; }
          .carouselContainer .contentContainer .search .autocomplete .university .infoContainer {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: baseline;
            padding: .5vw; }
            .carouselContainer .contentContainer .search .autocomplete .university .infoContainer h3 {
              margin: 0; }
            .carouselContainer .contentContainer .search .autocomplete .university .infoContainer h5 {
              margin: 0; }

@keyframes backgroundGradient {
  0% {
    -webkit-transform: translate(-10%, -50%) rotate(0deg);
    -ms-transform: translate(-10%, -50%) rotate(0deg);
    transform: translate(-10%, -50%) rotate(0deg); }
  50% {
    -webkit-transform: translate(-10%, -50%) rotate(180deg);
    -ms-transform: translate(-10%, -50%) rotate(180deg);
    transform: translate(-10%, -50%) rotate(180deg); }
  100% {
    -webkit-transform: translate(-10%, -50%) rotate(360deg);
    -ms-transform: translate(-10%, -50%) rotate(360deg);
    transform: translate(-10%, -50%) rotate(360deg); } }
