.card {
  width: 20vw;
  height: 25vw;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: white;
  margin: 1vw;
  color: black;
  box-shadow: 0 0 1vw -.5vw black;
  transition: all .2s ease-in-out; }
  .card:hover {
    box-shadow: 0 0 1vw -.2vw black;
    cursor: pointer; }
  .card .imgContainer {
    width: 10vw;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 10vw;
    border-radius: 50%;
    overflow: hidden;
    margin-bottom: 10%; }
    .card .imgContainer img {
      width: 150%; }
