.loadingContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 90vh;
  margin-top: -2vw; }

.button {
  font-weight: bold;
  padding: .5vw 1vw;
  border-radius: 20px;
  font-size: .8vw;
  border: none;
  background: white;
  text-decoration: none;
  color: black;
  transition: all .2s ease-in-out; }
  .button:hover {
    opacity: .8;
    cursor: pointer; }
  .button:visited {
    color: black; }

a {
  text-decoration: none; }

@keyframes backgroundBooklickColors {
  0% {
    background-color: #F6416C; }
  33% {
    background-color: #2ABDEF; }
  66% {
    background-color: #1FCFCB; }
  100% {
    background-color: #F6416C; } }

.navBarTop {
