.loadingContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 90vh;
  margin-top: -2vw; }

.button {
  font-weight: bold;
  padding: .5vw 1vw;
  border-radius: 20px;
  font-size: .8vw;
  border: none;
  background: white;
  text-decoration: none;
  color: black;
  transition: all .2s ease-in-out; }
  .button:hover {
    opacity: .8;
    cursor: pointer; }
  .button:visited {
    color: black; }

a {
  text-decoration: none; }

@keyframes backgroundBooklickColors {
  0% {
    background-color: #F6416C; }
  33% {
    background-color: #2ABDEF; }
  66% {
    background-color: #1FCFCB; }
  100% {
    background-color: #F6416C; } }

.popUp {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10;
  top: 0;
  background: rgba(72, 72, 72, 0.8); }
  .popUp div {
    background: white;
    padding: 2vw 3vw;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column; }
    .popUp div span {
      position: absolute;
      right: -2%;
      top: -10%;
      background: #F6416C;
      color: white;
      border-radius: 50%;
      width: 1vw;
      height: 1vw;
      display: flex;
      align-items: center;
      justify-content: center;
      padding: .3vw;
      transition: all .2s ease-in-out; }
      .popUp div span:hover {
        padding: .4vw;
        cursor: pointer; }
    .popUp div .button {
      background: #2ABDEF;
      color: white; }
